    var uidList = rectRoulette.GetRectItem('all').map(function (rect) { return rect.uid; });
    var nameList = rectRoulette.GetRectItem('all').map(function (rect) { return rect.name(); });
    nameList = nameList.filter(function (name, index) { return nameList.indexOf(name) == index; });
